@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700&display=swap);
body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root{--dark: rgb(21, 79, 104);--medium: rgb(7, 84, 104);--light: rgb(36, 167, 160);--xlight: rgb(75, 202, 224);--xxlight: rgb(234, 250, 248)}.day{--dark: rgb(29, 137, 225);--medium: rgb(124, 181, 225);--light: rgb(245, 136, 97);--gradient-dark: rgb(200, 235, 135);--xlight: rgb(248, 239, 134);--xxlight: rgb(255, 254, 225);--clouds: rgb(232, 245, 228)}.evening{--dark: rgb(129, 9, 83);--medium: rgb(196, 48, 68);--light: rgb(211, 108, 60);--gradient-dark: rgb(196, 48, 68);--xlight: rgb(245, 164, 89);--xxlight: rgb(255, 232, 218);--clouds: rgb(255, 232, 218)}.night{--dark: rgb(7, 54, 75);--medium: rgb(7, 84, 104);--light: rgb(36, 167, 160);--gradient-dark: rgb(7, 84, 104);--xlight: rgb(75, 202, 224);--xxlight: rgb(234, 250, 248);--clouds: rgb(234, 250, 248)}.temp-panel{display:flex;flex-flow:column;align-items:center;padding:30px;position:relative}.temp{margin:4px 0;font-size:3em;font-weight:700}.temp-conditions,.temp-location{margin:4px 0;font-size:1.4em;font-weight:300;position:relative;z-index:1}.temp-conditions-icon{margin-right:40px;position:absolute;right:0;width:100px;height:100px}.temp-convert{position:absolute;right:0;padding-right:inherit}.temp-convert button{background:none;color:white;border:none;outline:none}.temp-convert button.active{font-weight:700;text-shadow:0 0 3px #fff;font-size:1.1em}.temp-convert button:first-child{border-right:1px solid white}.temp-convert button:last-child{margin-left:-1px;border-left:1px solid white}.temp-convert button:hover:not(.active){color:rgb(75, 202, 224);color:var(--xlight)}

:root{--dark: rgb(21, 79, 104);--medium: rgb(7, 84, 104);--light: rgb(36, 167, 160);--xlight: rgb(75, 202, 224);--xxlight: rgb(234, 250, 248)}.day{--dark: rgb(29, 137, 225);--medium: rgb(124, 181, 225);--light: rgb(245, 136, 97);--gradient-dark: rgb(200, 235, 135);--xlight: rgb(248, 239, 134);--xxlight: rgb(255, 254, 225);--clouds: rgb(232, 245, 228)}.evening{--dark: rgb(129, 9, 83);--medium: rgb(196, 48, 68);--light: rgb(211, 108, 60);--gradient-dark: rgb(196, 48, 68);--xlight: rgb(245, 164, 89);--xxlight: rgb(255, 232, 218);--clouds: rgb(255, 232, 218)}.night{--dark: rgb(7, 54, 75);--medium: rgb(7, 84, 104);--light: rgb(36, 167, 160);--gradient-dark: rgb(7, 84, 104);--xlight: rgb(75, 202, 224);--xxlight: rgb(234, 250, 248);--clouds: rgb(234, 250, 248)}.conditions-panel{height:360px;position:relative;overflow:hidden}.conditions-gradient{width:100%;height:100%;background-image:-webkit-gradient(linear, left bottom, left top, from(rgb(75, 202, 224)), to(var(--gradient-dark)));background-image:linear-gradient(to top, rgb(75, 202, 224), var(--gradient-dark));background-image:-webkit-gradient(linear, left bottom, left top, from(var(--xlight)), to(var(--gradient-dark)));background-image:linear-gradient(to top, var(--xlight), var(--gradient-dark))}.conditions-sun{width:80px;height:80px;border-radius:50%;background-color:rgb(234, 250, 248);background-color:var(--xxlight);box-shadow:0 0 50px 10px rgb(234, 250, 248);box-shadow:0 0 50px 10px var(--xxlight);position:absolute;bottom:30%;right:25%}.conditions-clouds{margin-top:-20px;width:100%;height:80px;background:var(--clouds);position:absolute;top:0}.conditions-clouds::before{content:'';width:100%;height:180px;background:radial-gradient(ellipse at center, var(--clouds) 0%, var(--clouds) 60%, transparent 60%);background-size:200px 200px;background-repeat:repeat-x;background-position:0 center;position:absolute}.conditions-clouds::after{content:'';width:100%;height:180px;background:radial-gradient(ellipse at center, var(--clouds) 0%, var(--clouds) 30%, transparent 30%);background-size:200px 200px;background-repeat:repeat-x;background-position:-248% 0;position:absolute}.conditions-mountains{width:0;height:0;border-width:0 200px 100px 180px;border-style:solid;border-color:transparent transparent rgb(21, 79, 104) transparent;border-color:transparent transparent var(--dark) transparent;position:absolute;bottom:0;right:50%;-webkit-transform:translate(25%, 0);transform:translate(25%, 0)}.conditions-mountains::before,.conditions-mountains::after{content:'';width:0;height:0;border-width:0 180px 90px 160px;border-style:solid;border-color:transparent transparent rgb(7, 84, 104) transparent;border-color:transparent transparent var(--medium) transparent;position:absolute;top:10px;left:30px}.conditions-mountains::before{-webkit-transform:translate(-340px, 0);transform:translate(-340px, 0)}

.details-panel{letter-spacing:1px;display:flex;justify-content:space-around;padding:10px 0}.details-panel>li{display:flex;flex-flow:column;align-items:center}.details-panel>li div{padding:5px 0}.details-panel>li div:first-child{font-weight:bold}

.forecast-panel{display:flex;justify-content:space-around;padding:20px 0}.forecast-panel>li{display:flex;flex-flow:column;align-items:center}.forecast-temp{font-weight:bold}.forecast-conditions{padding-top:12px}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,input,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;font-size:100%;vertical-align:baseline;box-sizing:border-box}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

:root{--dark: rgb(21, 79, 104);--medium: rgb(7, 84, 104);--light: rgb(36, 167, 160);--xlight: rgb(75, 202, 224);--xxlight: rgb(234, 250, 248)}.day{--dark: rgb(29, 137, 225);--medium: rgb(124, 181, 225);--light: rgb(245, 136, 97);--gradient-dark: rgb(200, 235, 135);--xlight: rgb(248, 239, 134);--xxlight: rgb(255, 254, 225);--clouds: rgb(232, 245, 228)}.evening{--dark: rgb(129, 9, 83);--medium: rgb(196, 48, 68);--light: rgb(211, 108, 60);--gradient-dark: rgb(196, 48, 68);--xlight: rgb(245, 164, 89);--xxlight: rgb(255, 232, 218);--clouds: rgb(255, 232, 218)}.night{--dark: rgb(7, 54, 75);--medium: rgb(7, 84, 104);--light: rgb(36, 167, 160);--gradient-dark: rgb(7, 84, 104);--xlight: rgb(75, 202, 224);--xxlight: rgb(234, 250, 248);--clouds: rgb(234, 250, 248)}.temp-panel,.forecast-panel{background:rgb(21, 79, 104);background:var(--dark)}.conditions-panel{background-color:rgb(7, 84, 104);background-color:var(--medium)}.details-panel{background:rgb(36, 167, 160);background:var(--light)}body{font-family:'Lato', Arial, Helvetica, sans-serif;color:white}header{text-align:center;padding:10px}form{display:flex;width:480px;margin:0 auto 10px auto}form input{padding:20px 10px;outline:none;border:none;border-bottom:1px solid grey;flex-grow:1}form button{margin-left:10px;flex-basis:50px}main{width:480px;margin:0 auto;border-radius:30px;overflow:hidden}.loading{color:#888;width:480px;margin:0 auto}

